import React, { useState, useEffect } from 'react';
import { 
  Button, 
  Space, 
  NavBar, 
  Grid, 
  Card,
  Toast,
  Dialog,
  Divider,
  WaterMark
} from 'antd-mobile';
import { SoundOutline, AudioOutline, CloseCircleOutline } from 'antd-mobile-icons';
import { v4 as uuidv4 } from 'uuid';
import MobileNavButton from '../../components/MobileNavButton';
import apiClient from '../../services/apiClient';
import useTTS from '../../hooks/useTTS';
import useVoice from '../../hooks/useVoice';
import useIntent from '../../hooks/useIntent';
import './VoiceAIMobile.css';

// 
const SpeechRecognitionAPI = window.SpeechRecognition || window.webkitSpeechRecognition || null;

const VoiceAIMobile = () => {
  const [listening, setListening] = useState(false);
  const [processing, setProcessing] = useState(false);
  const [executing, setExecuting] = useState(false);
  const [speaking, setSpeaking] = useState(false);
  const [text, setText] = useState('');
  const [result, setResult] = useState('');
  const [sessionId, setSessionId] = useState('');
  const [confirmText, setConfirmText] = useState('');
  const [apiAvailable, setApiAvailable] = useState(true);
  const [waitingForConfirmation, setWaitingForConfirmation] = useState(false);
  const [currentToolId, setCurrentToolId] = useState('');
  const [currentParams, setCurrentParams] = useState({});
  const [originalQuery, setOriginalQuery] = useState('');
  
  // hooks
  const { speak, cancel, isSpeaking } = useTTS();
  const voice = useVoice(); // useVoice hookAPI
  const { classifyIntent } = useIntent(); // useIntent hook
  
  useEffect(() => {
    // API
    if (!SpeechRecognitionAPI) {
      setApiAvailable(false);
      Toast.show({
        icon: 'fail',
        content: '',
        duration: 3000,
      });
      return;
    }
    
    // ID
    setSessionId(uuidv4());
  }, []);
  
  // useEffectvoice.transcript
  useEffect(() => {
    if (waitingForConfirmation && voice.transcript) {
      console.log(":", voice.transcript);
      
      // useIntentclassifyIntent,
      const intent = classifyIntent(voice.transcript, originalQuery);
      console.log(":", intent);
      
      // IGNORE,
      if (intent === 'IGNORE') {
        console.log(",");
        return;
      }
      
      // 
      voice.stopListening();
      
      // 
      if (intent === 'CONFIRM') {
        // ,
        if (executing) {
          console.log(",");
          return;
        }
        
        // ,
        setTimeout(() => {
          // 
          if (currentToolId) {
            Toast.show({
              icon: 'loading',
              content: '...',
              duration: 0,
            });
            
            setWaitingForConfirmation(false);
            executeAction(currentToolId, currentParams).finally(() => {
              Toast.clear();
            });
          } else {
            console.log("ID,");
            Toast.show({
              content: ',',
            });
            setWaitingForConfirmation(false);
          }
        }, 300);
      } else if (intent === 'RETRY') {
        // 
        setWaitingForConfirmation(false);
        if (text) {
          Toast.show({
            content: '...',
          });
          processIntent(text);
        }
      } else if (intent === 'CANCEL') {
        Toast.show({
          icon: 'success',
          content: '',
        });
        setWaitingForConfirmation(false);
        setConfirmText('');
        setCurrentToolId('');
        setCurrentParams({});
      } else {
        // ,
        Toast.show({
          icon: 'info',
          content: ',',
        });
        setWaitingForConfirmation(false);
        setConfirmText('');
      }
    }
  }, [voice.transcript, waitingForConfirmation, classifyIntent, currentToolId, currentParams, text, originalQuery, executing]);
  
  // useEffectisSpeaking
  useEffect(() => {
    setSpeaking(isSpeaking);
    
    // TTS,
    if (!isSpeaking && waitingForConfirmation && confirmText) {
      console.log("TTS,...");
      
      // ,TTS
      // TTS/STT
      const delay = 2000; // 2,
      const timer = setTimeout(() => {
        // 
        // ,
        if (waitingForConfirmation && !voice.isListening && !processing && !executing) {
          console.log("...");
          voice.startListening();
          setListening(true);
        } else {
          console.log(",");
        }
      }, delay);
      
      // ,
      return () => clearTimeout(timer);
    }
  }, [isSpeaking, waitingForConfirmation, confirmText, voice, processing, executing]);
  
  // useEffectvoice.isListening
  useEffect(() => {
    setListening(voice.isListening);
    
    // ,
    if (!voice.isListening && voice.transcript && !waitingForConfirmation && !processing) {
      // ,
      const currentTranscript = voice.transcript;
      console.log(",:", currentTranscript);
      
      // ,
      setOriginalQuery(currentTranscript);
      
      // 
      setTimeout(() => {
        if (!processing) {
          setText(currentTranscript);
          processIntent(currentTranscript);
        }
      }, 200);
    }
  }, [voice.isListening, voice.transcript, waitingForConfirmation, processing]);

  // 
  useEffect(() => {
    if (voice.error) {
      console.error(":", voice.error);
      Toast.show({
        icon: 'fail',
        content: voice.error,
        duration: 2000,
      });
    }
  }, [voice.error]);
  
  // 
  const startListening = async () => {
    if (!apiAvailable) {
      Toast.show({
        icon: 'fail',
        content: '',
      });
      return;
    }
    
    // 
    setText('');
    setResult('');
    setConfirmText('');
    setCurrentToolId('');
    setCurrentParams({});
    setWaitingForConfirmation(false);
    
    // 
    if (voice.isListening) {
      await voice.stopListening();
      // 
      await new Promise(resolve => setTimeout(resolve, 500));
    }
    
    try {
      console.log("VoiceAIMobile: ...");
      setListening(true);
      voice.startListening();
    } catch (error) {
      console.error(':', error);
      setListening(false);
      Toast.show({
        icon: 'fail',
        content: '',
      });
    }
  };
  
  // 
  const processIntent = async (userText) => {
    if (processing) {
      console.log(",");
      return; // 
    }
    
    setProcessing(true);
    
    try {
      console.log(`: "${userText}"`);
      const response = await apiClient.interpret(userText, sessionId, 1);
      
      // 
      if (response.type === 'confirm' || (response.tool_calls && response.tool_calls.length > 0)) {
        // 
        const confirmMessage = response.confirmText || response.confirm_text || '';
        setConfirmText(confirmMessage);
        
        // ID
        let toolId = '';
        let params = {};
        
        if (response.tool_calls && response.tool_calls.length > 0) {
          // API
          const toolCall = response.tool_calls[0];
          toolId = toolCall.tool_id;
          params = toolCall.parameters || {};
        } else {
          // API
          toolId = response.action;
          params = response.params || {};
        }
        
        // ID,
        setCurrentToolId(toolId);
        setCurrentParams(params);
        
        // TTS
        setListening(false);
        if (voice.isListening) {
          await voice.stopListening();
        }
        
        // 
        setWaitingForConfirmation(true);
        
        // TTS,
        // 
        if (isSpeaking) {
          cancel();
          // 
          await new Promise(resolve => setTimeout(resolve, 300));
        }
        
        // 
        console.log(`: "${confirmMessage}"`);
        speak(confirmMessage);
        setSpeaking(true);
      } else if (response.content) {
        // 
        setResult(response.content);
        speak(response.content);
      } else {
        setResult(',');
        speak(',');
      }
    } catch (error) {
      console.error(':', error);
      Toast.show({
        icon: 'fail',
        content: error.message || ',',
      });
    } finally {
      // processing,
      setTimeout(() => {
        setProcessing(false);
      }, 300);
    }
  };
  
  // 
  const executeAction = async (toolId, params) => {
    // 
    if (executing) {
      console.log(",");
      return;
    }
    
    setExecuting(true);
    
    try {
      // ,
      setWaitingForConfirmation(false);
      
      console.log(`,ID: ${toolId}`);
      const response = await apiClient.execute(toolId, params, sessionId, 1);
      
      if (response.success) {
        // TTS
        cancel();
        
        // 
        const displayText = formatResultForDisplay(response.data);
        setResult(displayText);
        
        // 
        const speechText = formatResultForSpeech(response.data);
        
        // TTS,
        setTimeout(() => {
          console.log(`: "${speechText.substring(0, 30)}${speechText.length > 30 ? '...' : ''}"`);
          speak(speechText);
        }, 300);
      } else {
        const errorMessage = `: ${response.error?.message || ''}`;
        setResult(errorMessage);
        
        // TTS
        cancel();
        
        // 
        setTimeout(() => {
          speak(`,${response.error?.message || ''}`);
        }, 300);
      }
    } catch (error) {
      console.error(':', error);
      setResult(',');
      
      // TTS
      cancel();
      
      // 
      setTimeout(() => {
        speak(',,');
      }, 300);
    } finally {
      // 
      setTimeout(() => {
        setExecuting(false);
        // ID
        setCurrentToolId('');
        setCurrentParams({});
        // 
        setConfirmText('');
      }, 300);
    }
  };
  
  // 
  const formatResultForSpeech = (data) => {
    if (!data) return '';
    
    // TTS
    if (typeof data === 'object') {
      if (data.tts_message) return data.tts_message;
      if (data.summary) return data.summary;
    }
    
    // TTS,
    return formatResultForDisplay(data);
  };
  
  // 
  const formatResultForDisplay = (data) => {
    if (!data) return '';
    
    // 
    if (typeof data === 'string') return data;
    
    // 
    if (typeof data === 'object') {
      // 
      if (data.tts_message) return data.tts_message;
      if (data.message) return data.message;
      if (data.summary) return data.summary;
      if (data.result) return data.result;
      
      // 
      if (data.weather || data.temperature) {
        let result = '';
        if (data.location) result += `${data.location}`;
        if (data.weather) result += data.weather;
        if (data.temperature) result += `,${data.temperature}`;
        if (result) return result;
      }
      
      // 
      try {
        return JSON.stringify(data, null, 2);
      } catch (e) {
        return ',';
      }
    }
    
    return '';
  };
  
  // 
  const cancelSpeech = () => {
    cancel();
    setSpeaking(false);
    
    // ,
    if (waitingForConfirmation) {
      // ,
      setTimeout(() => {
        setWaitingForConfirmation(false);
        if (voice.isListening) {
          voice.stopListening();
        }
        setConfirmText('');
        setCurrentToolId('');
        setCurrentParams({});
        setListening(false);
      }, 300);
    }
  };
  
  // 
  const resetSession = async () => {
    // 
    cancel();
    
    // ,
    if (voice.isListening) {
      await voice.stopListening();
    }
    
    // 
    setText('');
    setResult('');
    setConfirmText('');
    setListening(false);
    setProcessing(false);
    setExecuting(false);
    setSpeaking(false);
    setWaitingForConfirmation(false);
    setCurrentToolId('');
    setCurrentParams({});
    
    // ID
    const newSessionId = uuidv4();
    setSessionId(newSessionId);
    console.log(',ID:', newSessionId);
    
    // 
    Toast.show({
      icon: 'success',
      content: '',
      duration: 1500,
    });
  };
  
  // 
  const handleDemoText = () => {
    const demoText = "";
    setText(demoText);
    processIntent(demoText);
  };

  return (
    <div className="voice-ai-container">
      <NavBar backArrow={false} style={{ 
        background: '#1677ff', 
        color: 'white',
        fontWeight: 'bold'
      }}> AI </NavBar>
      
      <div className="content-area">
        {text && (
          <Card title="" headerStyle={{ color: '#1677ff' }}>
            <div className="user-text">{text}</div>
          </Card>
        )}
        
        {confirmText && (
          <Card title="" headerStyle={{ color: '#722ed1' }}>
            <div className="confirm-text">{confirmText}</div>
          </Card>
        )}
        
        {waitingForConfirmation && voice.transcript && (
          <Card title="" headerStyle={{ color: '#1890ff' }}>
            <div className="user-text">{voice.transcript}</div>
          </Card>
        )}
        
        {result && (
          <Card title="" headerStyle={{ color: '#52c41a' }}>
            <div className="result-text">
              {(() => {
                try {
                  const jsonObj = JSON.parse(result);
                  // JSON,
                  return (
                    <pre style={{ 
                      whiteSpace: 'pre-wrap',
                      wordBreak: 'break-word',
                      overflowX: 'auto',
                      padding: '8px',
                      backgroundColor: '#f5f5f5',
                      borderRadius: '4px'
                    }}>
                      {JSON.stringify(jsonObj, null, 2)}
                    </pre>
                  );
                } catch (e) {
                  // JSON,
                  return <p style={{ lineHeight: '1.5' }}>{result}</p>;
                }
              })()}
            </div>
          </Card>
        )}
        
        {!text && !confirmText && !result && (
          <div style={{ 
            textAlign: 'center', 
            padding: '40px 20px',
            color: '#666' 
          }}>
            <SoundOutline style={{ fontSize: 48, color: '#1677ff' }} />
            <p style={{ marginTop: 16 }}></p>
          </div>
        )}
      </div>
      
      <div className="status-indicator">
        {listening && <div className="status listening">{waitingForConfirmation ? '' : ''}...</div>}
        {processing && <div className="status processing">...</div>}
        {executing && <div className="status executing">...</div>}
        {speaking && <div className="status speaking">...</div>}
        {waitingForConfirmation && !listening && !speaking && (
          <div className="status waiting">...</div>
        )}
        {!listening && !processing && !executing && !speaking && !waitingForConfirmation && (
          <div style={{ padding: '8px', color: '#666' }}>,</div>
        )}
      </div>
      
      <div className="control-area">
        <Grid columns={3} gap={8}>
          <Grid.Item>
            <Button
              block
              color="primary"
              size="large"
              loading={listening}
              disabled={processing || executing || speaking || waitingForConfirmation || !apiAvailable}
              onClick={startListening}
              icon={<SoundOutline />}
            >
              {listening ? '...' : ''}
            </Button>
          </Grid.Item>
          
          <Grid.Item>
            <Button
              block
              color="warning"
              size="large"
              disabled={!speaking && !waitingForConfirmation}
              onClick={cancelSpeech}
              icon={<CloseCircleOutline />}
            >
              {speaking ? '' : ''}
            </Button>
          </Grid.Item>
          
          <Grid.Item>
            <Button
              block
              color="success"
              size="large"
              disabled={!result}
              onClick={() => {
                try {
                  const resultData = JSON.parse(result);
                  speak(formatResultForSpeech(resultData));
                } catch(e) {
                  speak(result);
                }
              }}
              icon={<AudioOutline />}
            >
              
            </Button>
          </Grid.Item>
        </Grid>
        
        {/*  */}
        {(text || result || confirmText) && (
          <Button
            block
            color="default"
            size="middle"
            onClick={resetSession}
            style={{ marginTop: '12px' }}
          >
            
          </Button>
        )}
        
        {!apiAvailable && (
          <Button
            block
            color="primary"
            size="large"
            onClick={handleDemoText}
            style={{ marginTop: '12px' }}
          >
            
          </Button>
        )}
      </div>
      
      <div style={{ 
        padding: '8px 0', 
        textAlign: 'center',
        fontSize: '12px',
        color: '#999',
        backgroundColor: '#f5f5f5',
        borderTop: '1px solid #eee'
      }}>
        Echo AI   2025
      </div>
      
      <WaterMark content="Echo AI" />
      
      {/*  */}
      <div style={{ position: 'absolute', top: '12px', right: '12px' }}>
        <Button 
          size="mini" 
          onClick={() => window.location.href = '/classic'}
        >
          
        </Button>
      </div>
    </div>
  );
};

export default VoiceAIMobile; 